function activePor() {
  const contact = document.querySelector("#portfolio");
  contact.classList.add("active");
}
function itemBoxHover() {
  const boxWrap = document.querySelectorAll(".item.item_v1");
  // const hoverBox = documnet.querySelectorAll('.black-image-project-hover')
  for (var i = 0; i < boxWrap.length; i++) {
    boxWrap[i].addEventListener("mouseover", function () {
      this.classList.add("hover");
    });
    boxWrap[i].addEventListener("mouseout", function (e) {
      this.classList.remove("hover");
    });
  }
}

function headerDate() {
  var date = new Date();

  //
  var nowYear = date.getFullYear();

  //date Object
  var month = date.getMonth();

  var month_1 = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];

  //
  var clockDate = date.getDate();

  //
  var day = date.getDay();

  //       
  var week = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
  ];

  $(".date").html(
    "<span>" +
      week[day] +
      "," +
      "&nbsp;" +
      "</span>" +
      "<span>" +
      month_1[month] +
      "&nbsp;" +
      clockDate +
      "," +
      "&nbsp;" +
      "</span>" +
      "<span>" +
      nowYear +
      "</span>"
  );
}

